
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { ICountryForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import CountryForm from '@/views/system/localization/country/forms/CountryForm.vue'

export interface IData {
  model: ICountryForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: ICountryForm) => {
      return store.dispatch('country/createForm', dataForm)
    }) as ResponseFetching<Promise<ICountryForm>>

    const form = ref()

    return {
      saveForm,
      saveStatus,
      saveError,
      form
    }
  },

  components: {
    PageCard,
    CountryForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        name: 'New country',
        isoCode2: '',
        isoCode3: '',
        isPostcodeRequired: false,
        isActive: false
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'country' })
    },

    async handleSubmit () {
      if (this.form && await this.form.validate()) {
        const data: ICountryForm = await this.saveForm(this.model)
        if (typeof data !== 'undefined') {
          this.model = data
        }

        if (this.model?.id) {
          this.$router.push({ name: 'country-update', params: { id: this.model.id } })
        }
      }
    }
  }
})
